.b-vacancies__btns {
  margin-bottom: 25px;
  white-space: nowrap;
  max-width: 100%;
  overflow-x: auto; }
  .b-vacancies__btns .btn {
    float: none !important; }
  .b-vacancies__btns .btn + .btn {
    margin-left: -5px !important; }

.b-vacancies-tools {
  margin: 25px 0px 0px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; }
  .b-vacancies-tools > * {
    margin: 5px 0; }
  .b-vacancies-tools__share {
    font-size: 13px; }
  .b-vacancies-tools__socials {
    display: inline-block;
    vertical-align: middle;
    margin-left: 15px; }
